.vsc-initialized {
  margin: 0;
}

#root {
  position: relative;
}

.no-scroll {
  overflow: hidden;
}

.Container {
  position: absolute;
  top: 0;
  left: 0;
}

.Board {
  display: flex;
}

.Row {
  display: absolute;
}

.Tile {
  width: 40px;
  height: 40px;
  border: 1px solid;
  background-color: white;
}